/**
* style for radioes
*/
/* radio box */
.rpgui-content input[type=radio].rpgui-radio {
  display: none; }

.rpgui-content input[type=radio].rpgui-radio + label {
  background: url(/craeft/static/media/radio-off.31720e09.png) no-repeat;
  line-height: 24px;
  display: inline-block;
  background-size: auto 100%;
  padding-left: 34px;
  height: 24px;
  margin-top: 8px;
  margin-bottom: 8px; }

.rpgui-content input[type=radio].rpgui-radio:checked + label {
  background: url(/craeft/static/media/radio-on.287354ad.png) no-repeat;
  line-height: 24px;
  display: inline-block;
  background-size: auto 100%;
  padding-left: 34px;
  height: 24px; }

/* golden radio */
.rpgui-content .rpgui-radio.golden + label {
  background: url(/craeft/static/media/radio-golden-off.93eeffc4.png) no-repeat !important;
  background-size: auto 100% !important; }

.rpgui-content .rpgui-radio.golden:checked + label {
  background: url(/craeft/static/media/radio-golden-on.9509d6bf.png) no-repeat !important;
  background-size: auto 100% !important; }

/**
* for disabled elements
*/
/* disabled object */
.rpgui-disabled,
.rpgui-content :disabled,
.rpgui-content input[type=radio]:disabled + label,
.rpgui-content input[type=checkbox]:disabled + label,
.rpgui-content input[type=range]:disabled + .rpgui-slider-container,
.rpgui-content :disabled + .rpgui-dropdown-imp,
.rpgui-content :disabled + .rpgui-dropdown-imp + .rpgui-dropdown-imp,
.rpgui-content :disabled + .rpgui-list-imp {
  cursor: url(/craeft/static/media/default.ef2aceb9.png), auto;
  -webkit-filter: grayscale(1);
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  -webkit-filter: url(#greyscale);
